import gsap from "gsap";
import { useEffect } from "react";
import { useLocation } from "react-router-dom";

const ScrollToTop = () => {
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
    gsap.fromTo(".layout_content", { opacity: 0 }, { opacity: 1, duration: 1 });
  }, [pathname]);
  return null;
};

export default ScrollToTop;
