import React, { useEffect, useRef, useState } from "react";
import "./awards.scss";
import { Link, useNavigate } from "react-router-dom";
import gsap from "gsap";

const info = [
  {
    id: 1,
    name: "GIL DONG HONG",
    nameKr: "홍길동",
    description:
      "이황은 조선전기 성균관 대사성, 대제학, 지경연 등을 역임한 문신이자 학자이다. 1501년(연산군 7)에 태어나",
    img: "1696493305834.jpg",
  },
  {
    id: 2,
    name: "GIL DONG HONG",
    nameKr: "홍길동",
    description:
      "이황은 조선전기 성균관 대사성, 대제학, 지경연 등을 역임한 문신이자 학자이다. 1501년(연산군 7)에 태어나",
    img: "1623643953148.jpg",
  },
  {
    id: 3,
    name: "GIL DONG HONG",
    nameKr: "홍길동",
    description:
      "이황은 조선전기 성균관 대사성, 대제학, 지경연 등을 역임한 문신이자 학자이다. 1501년(연산군 7)에 태어나",
    img: "1698982314621.jpg",
  },
  {
    id: 4,
    name: "GIL DONG HONG",
    nameKr: "홍길동",
    description:
      "이황은 조선전기 성균관 대사성, 대제학, 지경연 등을 역임한 문신이자 학자이다. 1501년(연산군 7)에 태어나",
    img: "1623643953148.jpg",
  },
];
const Awards = () => {
  const ref = useRef();
  const awardsRef = useRef();
  const [width, setWidth] = useState(0);
  const [imgWidth, setImgWidth] = useState(0);
  const [imgSrc, setImgSrc] = useState("");
  const navigate = useNavigate();
  const onClick = (id) => {
    navigate(`/awards?id=${id}`);
  };

  const handleResize = () => {
    setWidth(awardsRef.current?.offsetWidth);
    setImgWidth(ref.current?.offsetWidth);
  };
  useEffect(() => {
    window.addEventListener("resize", handleResize, false);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    handleResize();
  }, []);

  const onMove = ({ clientX, clientY }) => {
    if (ref.current) {
      gsap.to(ref.current, {
        x: clientX >= width - imgWidth ? width - imgWidth : clientX - 150,
        y: clientY / 2,
      });
    }
  };

  const onHover = (img) => {
    window.addEventListener("pointermove", onMove);
    setImgSrc(img);
    return () => window.removeEventListener("pointermove", onMove);
  };

  const onLeave = () => {
    setImgSrc("");
    window.removeEventListener("pointermove", onMove);
  };
  return (
    <div className="page_awards">
      <div className="page_awards_head global_page_head global_border">
        <div>효인 수상자들</div>
        <div className="global_line"></div>
        <div>&nbsp;</div>
      </div>
      <div className="awards_list" ref={awardsRef}>
        {info.map((award) => (
          <div key={award.id} className="awards_list_item">
            <h2 className="awards_list_item-title">{award.name}</h2>
            <div className="awards_list_item-subtitle">{award.nameKr}</div>
            <div
              className="awards_list_item-description"
              onMouseOver={() => onHover(award.img)}
              onMouseLeave={onLeave}>
              <p>{award.description}</p>
            </div>
            <div className="awards_list_item-links">
              <Link
                className="awards_list_item-links_more"
                to={`/award/${award.id}`}>
                자세히 보기
              </Link>
              <a className="awards_list_item-links_mail" href="mailto:/">
                이메일 하기 <span className="ss13">→</span>
              </a>
            </div>
          </div>
        ))}
        <div className="awards_list_img" ref={ref}>
          {imgSrc && (
            <div className="full_img">
              <img src={require(`../../assets/${imgSrc}`)} alt="list_img" />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Awards;
