import React, { useRef } from "react";
import { useEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

const MainHead = () => {
  const ref = useRef();
  gsap.registerPlugin(ScrollTrigger);

  useEffect(() => {
    const scroll = ScrollTrigger.create({
      trigger: ref.current,
      start: "top center",
      end: "bottom top",
      toggleActions: "play none none reset",
      toggleClass: { targets: "header", className: "main_head_active" },
    });

    return () => {
      scroll.kill(true);
    };
  }, []);
  return (
    <div className="main_head">
      <div className="main_head_inner" ref={ref}>
        <div className="main_head_img load_img">
          <img src={require("../../assets/IE001894689_STD.jpg")} alt="img" />
        </div>
        <div className="main_head_message">
          <span>인</span>
          <span>간</span>
          <span>이</span>
          <span>&nbsp;</span>
          <span>되</span>
          <span>다</span>
          <span>.</span>
        </div>
        <div className="main_head_logo">
          <p>HYOIN</p>
          <p className="main_head_logo-small">FOUNDATION 효인재단</p>
        </div>
      </div>
    </div>
  );
};

export default MainHead;
