import React, { useEffect, useRef, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import "./header.scss";
import { useMediaQuery } from "react-responsive";
import gsap from "gsap";

const Header = () => {
  const ref = useRef();
  const tl = useRef();
  const [isOpen, setIsOpen] = useState(false);
  const location = useLocation();
  const pathname = location.pathname;
  const mobile = useMediaQuery({ query: "(max-width: 820px)" });
  const onClick = () => {
    setIsOpen(!isOpen);
  };

  useEffect(() => {
    if (mobile) {
      tl.current = gsap.timeline({
        paused: true,
        onStart: () => {
          gsap.set(ref.current, { display: "initial" });
        },
        onReverseComplete: () => {
          gsap.set(ref.current, { display: "none" });
        },
      });
      tl.current.to(ref.current, { x: "0%", duration: 0.5 });
    }
  }, [mobile]);

  useEffect(() => {
    if (isOpen && mobile) {
      tl.current?.play();
    } else {
      tl.current?.reverse();
    }
  }, [isOpen, mobile]);

  return (
    <>
      {!mobile && (
        <header>
          <nav className="header_nav">
            <ul>
              <li className="header_nav_item">
                <Link to="/about">About</Link>
              </li>
              <li className="header_nav_item">
                <Link to="/donations">donations</Link>
              </li>
              <li className="header_nav_item">
                <Link to="/awards">awards</Link>
              </li>
              <li className="header_nav_item">
                <Link to="/press">Press</Link>
              </li>
              <li className="header_nav_item">
                <Link to="/Contact">Contact us</Link>
              </li>
            </ul>
          </nav>
          <div
            className="header_logo"
            >
            <Link to="/">
              <div className="header_logo_inner">
                <div className="header_logo_inner-big">孝</div>
                <div className="header_logo_inner-big">HOYIN</div>
                <div className="header_logo_inner-small">
                  <span>FOUNDATION</span>
                  <span>효인재단</span>
                </div>
              </div>
            </Link>
          </div>
        </header>
      )}
      {mobile && (
        <>
          <div className="header_mobile">
            <button className="header_mobile_btn" onClick={onClick}>
              <span className="header_mobile_hamburger"></span>
            </button>
          </div>
          <div
            className={
              pathname === "/" ? "header_logo main_head" : "header_logo"
            }>
            <Link to="/">
              <div className="header_logo_inner">
                <div className="header_logo_inner-big">孝</div>
                <div className="header_logo_inner-big">HOYIN</div>
                <div className="header_logo_inner-small">
                  <span>FOUNDATION</span>
                  <span>효인재단</span>
                </div>
              </div>
            </Link>
          </div>
          <div className="mobile_menu" ref={ref}>
            <div className="mobile_menu_inner">
              <div className="mobile_menu-title">
                <span>MENU</span>
                <button className="mobile_menu-close" onClick={onClick}>
                  CLOSE
                </button>
              </div>
              <ul className="mobile_menu-nav" onClick={onClick}>
                <li>
                  <Link to="/about">
                    About <span className="ss13">→</span>
                  </Link>
                </li>
                <li>
                  <Link to="/awards">
                    awards<span className="ss13">→</span>
                  </Link>
                </li>
                <li>
                  <Link to="/press">
                    Press<span className="ss13">→</span>
                  </Link>
                </li>
                <li>
                  <Link to="/donations">
                    donations<span className="ss13">→</span>
                  </Link>
                </li>
                <li>
                  <Link to="/Contact">
                    Contact us<span className="ss13">→</span>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
          {isOpen && <div className="mobile_menu_overlay"></div>}
        </>
      )}
    </>
  );
};

export default Header;
